import React, { Component } from 'react';
import { geoMercator, geoPath } from 'd3-geo';
import { scaleLinear, scaleSequential } from 'd3-scale';
import { tile as d3tile } from 'd3-tile';
import { interpolateYlOrRd } from 'd3-scale-chromatic';
import * as topojson from 'topojson-client';
import Place from '../place.svg';

import RadiusTable from './RadiusTable';

import './SVGMap.css';

const MapHeader = ({ selected, radius }) => (
  <div className="map-header">
    <h2>{selected !== null ? selected.properties.name : 'スタジアム未選択'}</h2>
    <div>
      {selected === null ||
      selected.properties.club ===
        'なし' ? null : selected.properties.club.split('&').length > 1 ? (
        <ul>
          {selected.properties.club
            .split('&')
            .map((cl, i) => (
              <li key={i}>{`${cl}(${
                selected.properties.category.split('&')[i]
              })`}</li>
            ))}
        </ul>
      ) : (
        <p>{`${selected.properties.club}(${selected.properties.category})`}</p>
      )}
      {selected !== null ? (
        <RadiusTable feature={selected} radius={radius} />
      ) : null}
    </div>
  </div>
);

const MapLegend = ({ nums, radiusScale, fillScale }) => (
  <ul className="legend">
    {nums.map((d, i, arr) => (
      <li key={i} style={{ width: `${100 / arr.length}%` }}>
        <svg width="24" height="24">
          <circle
            cx="12"
            cy="12"
            r={radiusScale(d)}
            fill={fillScale(d)}
            stroke={fillScale(d)}
            fillOpacity={0.6}
          />
        </svg>
        <p>{d}</p>
      </li>
    ))}
  </ul>
);

const MapFooter = ({
  radiusScale,
  fillScale,
  dataVisibility,
  toggleDataVisibility
}) => (
  <div className="map-footer">
    <MapLegend
      nums={[100, 250, 500, 750, 1000, 1500]}
      radiusScale={radiusScale}
      fillScale={fillScale}
    />
    <div className="map-function">
      <ul>
        <li
          onClick={() => {
            toggleDataVisibility();
          }}
        >
          人口を{dataVisibility ? '非表示に' : '表示'}する
        </li>
      </ul>
    </div>
  </div>
);

class SVGMap extends Component {
  constructor(props) {
    super(props);

    this.cache = {};
    this.heightReducer = 128;
    this.mag = 1.5;
    this.state = {
      dataVisibility: true,
      size: {
        width: window.innerWidth,
        height: window.innerHeight - 128
      },
      topojson: null
    };
    this.projection = geoMercator();
    this.path = geoPath();
    this.radiusScale = scaleLinear()
      .domain([0, 1000])
      .range([2, 8]);
    this.fillScale = scaleSequential(interpolateYlOrRd).domain([0, 1500]);
    this.buffers = [];
    this.points = [];
    this.tiles = [];

    this.fetchTopojson = this.fetchTopojson.bind(this);
    this.setupMap = this.setupMap.bind(this);
  }

  fetchTopojson(feature) {
    if (this.cache.hasOwnProperty(feature.properties.id)) {
      const nextTopo = this.cache[feature.properties.id];
      this.setState({ topojson: nextTopo });
    } else {
      fetch(
        `https://cieloazul310.github.io/dataset/stadiums/topojson/${
          feature.properties.id
        }.topojson`
      )
        .then(res => res.json())
        .then(json => {
          this.cache[feature.properties.id] = json;
          this.setState({ topojson: json });
        });
    }
  }

  setupMap() {
    if (this.state.topojson !== null) {
      const json = this.state.topojson;
      const features = topojson.feature(json, json.objects.radius5000).features;
      const buffer = features[features.length - 1];
      this.buffers = [];
      for (let prop in json.objects) {
        const ft = topojson.feature(json, json.objects[prop]).features;
        this.buffers.push(ft[ft.length - 1]);
      }
      this.buffers.sort((a, b) => b.properties.radius - a.properties.radius);
      this.points = features.slice(0, features.length - 1);

      this.projection.fitExtent(
        [[10, 10], [this.state.size.width - 10, this.state.size.height - 10]],
        buffer
      );
      this.path = geoPath(this.projection);
      this.tiles = d3tile()
        .size([
          this.state.size.width * this.mag,
          this.state.size.height * this.mag
        ])
        .scale(this.projection.scale() * 2 * Math.PI * this.mag)
        .translate(this.projection([0, 0]).map(v => v * this.mag))();
    }
  }

  componentDidMount() {
    window.addEventListener('resize', () => {
      console.log('resize');
      const size = { width: window.innerWidth, height: this.state.size.height };

      this.setState(prevState => ({
        size: {
          width: window.innerWidth,
          height: prevState.size.height
        }
      }));
    });
  }

  componentDidUpdate(prevProps, prevState) {
    if (prevProps.state.selected !== this.props.state.selected) {
      this.fetchTopojson(this.props.state.selected);
    }
  }

  render() {
    this.setupMap();
    return (
      <div>
        <MapHeader
          selected={this.props.state.selected}
          radius={this.props.state.radius}
        />
        <div>
          <svg width={this.state.size.width} height={this.state.size.height}>
            {this.props.state.selected === null ? (
              <g>
                <rect
                  width={this.state.size.width}
                  height={this.state.size.height}
                  fill="#333"
                />
                <text
                  textAnchor="middle"
                  x={this.state.size.width / 2}
                  y={this.state.size.height / 2}
                  fill="#999"
                >
                  {'NO MAP'}
                </text>
              </g>
            ) : null}
            <filter id="desaturate">
              <feColorMatrix
                type="matrix"
                values={
                  '0.3333 0.3333 0.3333 0 0 0.3333 0.3333 0.3333 0 0 0.3333 0.3333 0.3333 0 0 0 0 0 1 0'
                }
              />
            </filter>
            <g className="baseLayer">
              {this.tiles.length > 0
                ? this.tiles.map((tile, i) => (
                    <image
                      key={i}
                      xlinkHref={`https://cyberjapandata.gsi.go.jp/xyz/std/${
                        tile.z
                      }/${tile.x}/${tile.y}.png`}
                      x={
                        (tile.x + this.tiles.translate[0]) *
                        this.tiles.scale /
                        this.mag
                      }
                      y={
                        (tile.y + this.tiles.translate[1]) *
                        this.tiles.scale /
                        this.mag
                      }
                      width={this.tiles.scale / this.mag}
                      height={this.tiles.scale / this.mag}
                      filter="url(#desaturate)"
                    />
                  ))
                : null}
            </g>
            <g
              className="points"
              style={{
                display: this.state.dataVisibility ? 'inline' : 'none'
              }}
            >
              {this.points.length > 0
                ? this.points.map((point, i) => (
                    <circle
                      key={i}
                      cx={this.projection(point.geometry.coordinates)[0]}
                      cy={this.projection(point.geometry.coordinates)[1]}
                      fill={this.fillScale(point.properties.val)}
                      r={this.radiusScale(point.properties.val)}
                      fillOpacity={0.6}
                      stroke={this.fillScale(point.properties.val)}
                      style={{ mixBlendMode: 'multiply' }}
                    />
                  ))
                : null}
            </g>
            <g className="buffers">
              {this.buffers.length > 0
                ? this.buffers.map((buffer, i) => (
                    <path
                      key={i}
                      className={
                        buffer.properties.radius === this.props.state.radius
                          ? 'active'
                          : null
                      }
                      d={this.path(buffer)}
                      fill="none"
                    />
                  ))
                : null}
            </g>
            {this.props.state.selected ? (
              <image
                xlinkHref={Place}
                x={
                  this.projection(
                    this.props.state.selected.geometry.coordinates
                  )[0]
                }
                y={
                  this.projection(
                    this.props.state.selected.geometry.coordinates
                  )[1]
                }
                transform="translate(-11.5, -22)"
              />
            ) : null}
          </svg>
        </div>
        <MapFooter
          radiusScale={this.radiusScale}
          fillScale={this.fillScale}
          dataVisibility={this.state.dataVisibility}
          toggleDataVisibility={() => {
            this.setState(prevState => ({
              dataVisibility: !prevState.dataVisibility
            }));
          }}
        />
      </div>
    );
  }
}

export default SVGMap;
