import React from 'react';
import { format as d3format } from 'd3-format';

import RadiusTable from './RadiusTable';
import './Row.css';

const format = d3format(',');

const Row = props => {
  const feature = props.feature;
  const { name, club, category } = feature.properties;
  return (
    <div className={props.selected ? 'row row-selected' : 'row'}>
      <div className="row-head">
        <div className="grid row-select-button">
          <input
            type="radio"
            checked={props.selected}
            onChange={() => {
              props.onFeatureSelect(feature);
            }}
          />
        </div>
        <div className="grid row-index">
          <span>{props.index + 1}</span>
        </div>
        <div
          className="grid row-name"
          onClick={() => {
            props.onFeatureToggle(feature);
          }}
        >
          <span>{name}</span>
        </div>
        <div className="grid row-value">
          <span>
            {format(feature.properties[`radius${props.radius}`]) + '人'}
          </span>
        </div>
      </div>
      <div className="row-dataField" hidden={!props.toggled}>
        <div>
          {club === 'なし' ? null : club.split('&').length > 1 ? (
            <ul>
              {club
                .split('&')
                .map((cl, i) => (
                  <li key={i}>{`${cl}(${category.split('&')[i]})`}</li>
                ))}
            </ul>
          ) : (
            <p>{`${club}(${category})`}</p>
          )}
          <RadiusTable radius={props.radius} feature={feature} />
        </div>
      </div>
    </div>
  );
};
export default Row;
