import React from 'react';

import Row from './Row';
import './Table.css';

const TableFunctions = ({
  distances,
  radius,
  sort,
  toggleSort,
  onChangeRadius
}) => (
  <div>
    <ul>
      {distances.map((d, i) => (
        <li
          key={i}
          onClick={() => {
            onChangeRadius(d.value);
          }}
          className={
            d.value === radius ? 'distance active' : 'distance'
          }
        >
          {d.label}
        </li>
      ))}
      <li
        onClick={() => {
          toggleSort();
        }}
      >
        {sort === 'asc' ? '少ない順' : '多い順'}
      </li>
    </ul>
  </div>
);

const TableHeader = ({ state, toggleSort, onChangeRadius }) => (
  <div className="table-header">
    <h2>
      {`スタジアムから${
        state.distances[state.distances.map(d => d.value).indexOf(state.radius)]
          .label
      }圏内の人口`}
    </h2>
    <TableFunctions
      distances={state.distances}
      radius={state.radius}
      sort={state.sort}
      onChangeRadius={onChangeRadius}
      toggleSort={toggleSort}
    />
  </div>
);

const Table = (props) => {
    const sortValue = props.state.sort === 'desc' ? -1 : 1;
    const sortKey = `radius${props.state.radius}`;
    const features = props.state.features.sort(
      (a, b) => sortValue * (a.properties[sortKey] - b.properties[sortKey])
    );
    return (
      <div className="table">
        <TableHeader state={props.state} toggleSort={props.toggleSort} onChangeRadius={props.onChangeRadius} />
        <div>
          {features.map((feature, i) => (
            <Row
              key={i}
              feature={feature}
              onFeatureSelect={props.onFeatureSelect}
              onFeatureToggle={props.onFeatureToggle}
              selected={
                props.state.selected !== null &&
                props.state.selected.properties.id ===
                  feature.properties.id
              }
              index={i}
              toggled={
                props.state.toggled.indexOf(feature.properties.id) >= 0
              }
              radius={props.state.radius}
            />
          ))}
        </div>
      </div>
    );
  }

export default Table;
