import 'babel-polyfill';

import React, { Component, Fragment } from 'react';
import Table from './component/Table';
import SVGMap from './component/SVGMap';
import Attribution from './component/Attribution';

import './App.css';

const HeaderMenu = ({ page, onMenuClicked }) => (
  <header>
    {[{ menu: 'table', label: 'TABLE' }, { menu: 'map', label: 'MAP' }].map(
      (obj, i) => (
        <div
          key={i}
          className={page === obj.menu ? 'menu active' : 'menu'}
          onClick={() => {
            onMenuClicked(obj.menu);
          }}
        >
          {obj.label}
        </div>
      )
    )}
  </header>
);

const Container = ({ component, hidden }) => (
  <div className="container" hidden={hidden}>
    <div className="content">
      {component}
      {Attribution}
    </div>
  </div>
);

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      distances: [
        { label: '500m', value: 500 },
        { label: '1.0km', value: 1000 },
        { label: '1.5km', value: 1500 },
        { label: '3.0km', value: 3000 },
        { label: '5.0km', value: 5000 }
      ],
      page: 'table',
      selected: null,
      radius: 5000,
      toggled: [],
      sort: 'desc',
      features: props.data.features
    };
    this.changeRadius = this.changeRadius.bind(this);
    this.onFeatureToggle = this.onFeatureToggle.bind(this);
    this.onFeatureSelect = this.onFeatureSelect.bind(this);
    this.toggleSort = this.toggleSort.bind(this);
  }

  toggleSort() {
    this.setState(prevState => ({
      sort: prevState.sort === 'desc' ? 'asc' : 'desc'
    }));
  }

  changeRadius(radius) {
    this.setState({
      radius: radius
    });
  }

  onFeatureSelect(feature) {
    this.setState(prevState => ({
      selected:
        prevState.selected !== null &&
        prevState.selected.properties.id === feature.properties.id
          ? null
          : feature
    }));
  }

  onFeatureToggle(feature) {
    const id = feature.properties.id;
    this.setState(prevState => ({
      toggled:
        prevState.toggled.indexOf(id) < 0
          ? [...prevState.toggled, id]
          : prevState.toggled.filter(d => d !== id)
    }));
  }

  render() {
    return (
      <div className="app">
        <HeaderMenu
          page={this.state.page}
          onMenuClicked={menu => {
            this.setState({ page: menu });
          }}
        />
        <Container
          hidden={this.state.page !== 'table'}
          component={
            <Table
              onFeatureSelect={this.onFeatureSelect}
              onFeatureToggle={this.onFeatureToggle}
              toggleSort={this.toggleSort}
              onChangeRadius={this.changeRadius}
              state={this.state}
            />
          }
        />
        <Container
          hidden={this.state.page !== 'map'}
          component={<SVGMap state={this.state} />}
        />
      </div>
    );
  }
}

export default App;
